$font-family-base:            	   'iranyekan';
$headings-font-family:        		     'peyda';
$font-size-base :                         14px;
$line-height-base:                           2;
$lead-font-size:        $font-size-base * 1.15;

$h1-font-size:                $font-size-base * 2.25;
$h2-font-size:                $font-size-base * 2;
$h3-font-size:                $font-size-base * 1.75;
$h4-font-size:                $font-size-base * 1.5;
$h5-font-size:                $font-size-base * 1.25;
$h6-font-size:                $font-size-base;

$font-sizes: (
  1: $h1-font-size,
  2: $h2-font-size,
  3: $h3-font-size,
  4: $h4-font-size,
  5: $h5-font-size,
  6: $h6-font-size
);